<script>
export default {
  name: 'MatchingCategory',
  created(){
      this.$store.commit("set", ["pageInfo", "매칭"])
      this.$store.commit("set", ["pageRoute", ""])
  },
  data () {
    return {
      myMajorSample: {
        title: "캡스톤 디자인 팀 구해요~!",
        sub_title: "스마트 앱 개발 계획인데 같이 하실 분!"
      },
      myUnivSample: {
        title: "같이 밥 먹을 사람!",
        sub_title: "배고파아아아아"
      },
      friendsSample: {
        title: "나랑 친구해 줄 사람",
        sub_title: "친구가 없어"
      },
      clubSample: {
        title: "봉사 동아리 인원 모집 중입니다",
        sub_title: "꿀 동아리 보장"
      }
    }
  },
  methods: {
    color (value) {
      let $color
      if (value <= 25) {
        $color = 'info'
      } else if (value > 25 && value <= 50) {
        $color = 'success'
      } else if (value > 50 && value <= 75) {
        $color = 'warning'
      } else if (value > 75 && value <= 100) {
        $color = 'danger'
      }
      return $color
    },
    gotoLogin(){
        this.$router.replace({ path: '/pages/login' })
    },
    gotoMyMajor(){
        this.$router.push({ name: 'CategoryMyMajor' })
    },
    gotoMyUniv(){
        this.$router.push({ name: 'CategoryMyUniv' })
    },
  }
}
</script>
